.contact-part {
    margin-top: 9.5rem;
}

.contact-header-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact-header-text h2 {
    font-size: 40px;
    font-weight: 600;
    color: #28231d;
}

.contact-header-text span {
    color: #5a5959;
}


.contact-content-container{
    display: flex;
    flex-direction: row;
align-items: center;
    justify-content: space-evenly;
    margin-top: 2rem;
}
.contact-social-media-part{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.contact-social-media-part h4{
    color: #28231d;
    font-size: 20px;
}
.contact-social-media-part div{
    border: 1px solid rgba(0,0,0,0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: #5a5959;
    margin-top: 1rem;
    font-size: 14px;
    width: 300px;
}

.contact-social-media-part div h4{
    color: #28231d;
    font-size: 16px;
}
.contact-social-media-part div label{
    font-size: 25px;
}
.contact-social-media-part p{
    margin-top: 1rem;
}

.contact-form-part form{
    display: flex;
    flex-direction: column;
    align-items: center;
   

}

.contact-form-part  form h4{
    color: #28231d;
    font-size: 20px;
}

.input-container input{
    width:300px;
    height: 30px;
    border: 1px solid rgba(0,0,0,0.3);
    background: none;
    margin-top: 1rem;
    color:#5a5959;
    border-radius: 0.75rem;
    padding: 1.5rem;
}
.input-container p{
    font-size: 13px;
    padding: 0.1rem;
    color:red;
}
.comment{
    width:300px;
    height: 130px;
    resize: none;
    border: 1px solid rgba(0,0,0,0.3);
    background: none;
    margin-top: 1rem;
    color:#5a5959;
    border-radius: 0.75rem;
    padding: 1.5rem;
}

.form-submit-button{
    margin-top: 1rem;
    width: 120px;
    height: 50px;
    background-color: #28231d;
    color:white;
    border-radius: 1rem;
    font-size: 20px;
    cursor: pointer;
    text-align: center;
}

.form-submit-button:hover{
    background-color: black;
}
.error-massage{
    color:red;
    font-size: 13px;
}


/* Media query for tablets */
@media (max-width: 768px) {
    .contact-content-container {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  
    .contact-social-media-part div {
      width: 80%;
      max-width: 300px;
      margin-top: 1rem;
    }
  
    .input-container input,
    .comment {
      width: 80%;
    }
  }
  
  /* Media query for mobile devices */
  @media (max-width: 480px) {
    .contact-header-text h2 {
      font-size: 32px;
    }
  
    .contact-social-media-part div {
      width: 90%;
    }
  
    .input-container input,
    .comment {
      width: 90%;
    }
  
    .form-submit-button {
      width: 60%;
      height: 40px;
    }
  }