.education-header-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.education-header-text h2 {
    font-size: 40px;
    font-weight: 600;
    color: #28231d;
}

.education-header-text span {
    color: #5a5959;
}

.education-part {
    margin-top: 9.5rem;
}


.education-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 4rem;
}
.education-data{
    
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    column-gap: 1.5rem;
    color: #5a5959;
    gap: 1rem;
    font-size: 15px;
   
}

.education-title{
    font-size: 17px;
    color: #28231d;
}

.education-rounder {
    display: inline-block;
    width: 12px;
    height: 12px;
    background-color: #28231d;
    border-radius: 50%;
    animation: rounderAnimation 1s infinite; /* 1s duration, infinite loop */
  }
.education-line{
    display: block;
    width: 1px;
    height: 100%;
    background-color: #28231d;
    transform: translate(6px,-7px);
}

@keyframes rounderAnimation {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  

  
@media (max-width: 480px) {

    .education-data{
        font-size: 13px;
        margin-left: 0.5rem;
    }
    .education-title{
        font-size: 15px;
    }
}
